.home__container
{
  row-gap: 7rem;
}
.home__content
{
  display: flex;
    padding-top: 5.5rem;
    column-gap: 2rem;
    align-items: center;
}
.home__description
{
  max-width: initial;
  margin-bottom: var(--mb-2-5);
}
.home__social
{
 display: grid;
 grid-template-columns: max-content;
 row-gap: 1rem;
}
.home__title
{
    font-size: var(--big-font-size);
    margin-bottom: var(--mb-0-25);
}
.home__hand
{
    width: 38px;
    height: 38px;
    margin-left: 0.4rem;
}
.home__subtitle
{
    position: relative;
    font-size: var(--h3-font-size);
    padding-right: 5.4rem;
    font-weight: var(--font-normal);
    margin-bottom: var(--mb-1);

}
.home__descrition
{
   max-width: 400px;
   margin-bottom: var(--mb-3); 
}
.home__social-icon
{
    font-size: 1.9rem;
    color: var(--title-color);
&:hover
{
    color: var(--title-color-dark);
}
}

.home__img
{
    background:url("../../assets/profile.jpg");
    background-repeat: no-repeat;
    box-shadow: inset 0 0 0 9px rgb(255 255 255/30%);
    order: 1;
    justify-self: center;
    width: 300px;
    height: 300px;
    background-size: cover;
    animation: profile__animate 8s ease-in-out infinite 1s;

}
@keyframes profile__animate
{
  0%
  {
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
  }  
  50%
  {
    border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
  }
  100%
  {
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
  }
}
.home__scroll
{
  margin-left: 9.25rem;
}
.wheel
{
  animation: scroll 2s ease infinite;
}
@keyframes scroll
{
  0%
  {
    transform: translateY(0);
  }
  30%
  {
    transform: translateY(3.7rem);

  }
}
.home__scroll-name
{
  color: var(--title-color);
  font-weight: var(--font-medium);
  margin-right: var(--mb-0-25);
  margin-left: 2px;
}
.home__scroll-arrow
{
  font-size: 1.25rem;
  color: var(--title-color);
}
@media screen and(max-width: 962px) {
.home__content
{
  display: flex;
  flex-direction: column;
}  
.home__hand
{
  width: 26px;
  height: 26px;
}
.home__subtitle
{
  padding-left: 3.75rem;
  margin-bottom: var(--mb-1);
}
.home__description
{
  max-width: initial;
  margin-bottom: var(--mb-2-5);
}
.home__img{
width: 250px;
height: 250px;  
box-shadow: inset 0 0 0 9px rgb(255 255 255/30%);
}
.home__scroll
{
  margin-left: 7.5rem;
}
}
@media screen and (max-width:768px){
.home__content
 {
  display: flex;
  flex-wrap: wrap;
  padding-top: 3.5rem;
 }
 .home__content:nth-child(1)
 {
   order: 1;
 }
 .home__img
 {
  order: initial;
  margin: 0px auto;
  
 } 
 .home__data
 {
  display: flex;
  flex-direction: column;
  margin: 0px 0px 0px 20px;

 }
 .button
 {
  width: 160px;
 }
 .home__img
 {
  box-shadow: inset 0 0 0  6px rgb(255 255 255/30%);
  width: 200px;
  height: 200px;  
}
.home__scroll
{
  display: none;
}
.section
{
  padding: 2rem 0 4rem;
}
}
@media screen and(max-width:350px)
{
  :root
  {
    --big-font-size:3.25rem;
  }
.home__img
{
   width: 100%;
  height: 100%;
}
.home__hand
{
  width: 22px;
  height: 22px;
}
.home__social
{
  position: fixed;

}
}
@media screen and(max-width:992px)
{
  .home__content
  {
    display: flex;
   grid-template-columns: 100px repeat(2,1fr);
   column-gap: 1.25rem; 
  }
  .home__hand
  {
    width: 26px;
    height: 26px;
  }
  .home__subtitle{
   padding-left: 3.75rem;
   margin-bottom: var(--mb-1); 
  }
  .home__description
  {
    max-width: initial;
    margin-bottom: var(--mb-2-5);
  }
  .container
  {
    margin-left: var(--mb-1-5);
    margin-right: var(--mb-1-5);
  }
  button
  {
    padding: 1rem 1.7rem;
  }
  .button__icon
  {
    width: 22px;
    height: 22px;
  }
  .home__img
  {
    width: 250px;
    height: 250px;
    box-shadow: inset 0 0 0 9px rgb(255 255 255/30%);

  }
  .home__scroll
  {
    margin-left: 7.5rem;
  }
}